@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
:root {
  /* Primary */
  --Blue: hsl(246, 80%, 60%);
  --Lightorange: hsl(15, 100%, 70%); /* (work) */
  --Softblue: hsl(195, 74%, 62%); /* (play) */
  --Lightred: hsl(348, 100%, 68%); /* (study) */
  --Limegreen: hsl(145, 58%, 55%); /* (exercise) */
  --Violet: hsl(264, 64%, 52%); /* (social) */
  --Softorange: hsl(43, 84%, 65%); /* (self care) */

  /* Neutral */
  --Verydarkblue: hsl(226, 43%, 10%);
  --Darkblue: hsl(235, 46%, 20%);
  --Desaturatedblue: hsl(235, 45%, 61%);
  /* --Pale-Blue: hsl(236, 100%, 87%); */
  --Pale-Blue: hsla(236, 100%, 87%, 0.5);
}
* {
  margin: 0;
}

body {
  font-size: 18px;
  font-family: "Rubik", sans-serif;
}

.User_container__27H0n {
  background-color: var(--Darkblue);
  border-radius: 15px;
}
.User_user__2JArK {
  background-color: var(--Blue);
  padding: 35px 30px;
  display: flex;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  border-radius: 15px;
}
.User_user__2JArK img {
  height: 75px;
  border: 3px solid white;
  border-radius: 50%;
}
.User_userData__3bjJN p {
  color: var(--Pale-Blue);
  font-size: 16px;
  margin-bottom: 5px;
}
.User_userData__3bjJN h1 {
  color: white;
  font-size: 25px;
  font-weight: 300;
}
.User_dateOptions__3QqcL {
  display: flex;
  justify-content: space-evenly;
  padding: 30px 0;
}
.User_dateOptions__3QqcL p {
  color: var(--Pale-Blue);
  cursor: pointer;
}
.User_dateOptions__3QqcL p:hover {
  color: white;
}

/* State */
.User_activeDate__1E5gv {
  color: white !important;
}

/* Iphone XS MAX */
@media (min-width: 414px) {
  .User_user__2JArK img {
    height: 80px;
  }
}

/* >= 550px */
@media (min-width: 550px) {
  .User_user__2JArK {
    justify-content: center;
    padding: 35px 30px;
    grid-gap: 35px;
    gap: 35px;
  }
  .User_user__2JArK img {
    height: 100px;
  }
  .User_userData__3bjJN p {
    font-size: 18px;
  }
  .User_userData__3bjJN h1 {
    font-size: 35px;
  }
  .User_dateOptions__3QqcL p {
    font-size: 20px;
  }
