/* Ipad */
@media (min-width: 768px) {
  .User_userData__3bjJN h1 {
    font-size: 45px;
  }
  .User_dateOptions__3QqcL p {
    font-size: 22px;
  }
}

/* Desktop */
@media (min-width: 1024px) {
  .User_container__27H0n {
    width: 30%;
  }
  .User_user__2JArK {
    padding: 35px 30px 45px 30px;
    flex-direction: column;
    align-items: flex-start;
  }
  .User_user__2JArK img {
    height: 70px;
  }
  .User_userData__3bjJN p {
    font-size: 13px;
  }
  .User_userData__3bjJN h1 {
    font-size: 32px;
  }
  .User_dateOptions__3QqcL {
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    padding: 30px;
  }
  .User_dateOptions__3QqcL p {
    font-size: 16px;
  }
}

